<template>
  <div>
    <a-menu
      mode="inline"
      v-model:selectedKeys="selectedKeys"
      :inline-collapsed="collapsed"
    >
      <a-menu-item
        v-for="item in machineMenu"
        :key="item.dateAdded + ''"
        @click="getClickMenu(item)"
      >
        <span>{{ item.title }}</span>
      </a-menu-item>
    </a-menu>
  </div>
</template>

<script>
import chromeGiteeUtil from "../../chrome_lib/chrome_gitee_util";

export default {
  name: "homeMenu",
    props: {
    edit: Boolean,
  },
  data() {
    return {
      selectedKeys: [],
      machineMenu: [],
    };
  },
  mounted() {
    chromeGiteeUtil.getBookMarks(this.edit).then((v) => {
      this.machineMenu = v;
      this.$emit("getShowMenu", this.machineMenu[0]);
      this.selectedKeys = [this.machineMenu[0].dateAdded + ""];
    }).catch(err => {
      console.log('获取书签出错:', err);
    });
  },
  methods: {
    getClickMenu(data) {
      this.$emit("getShowMenu", data);
    },
  },
};
</script>

